export const controller = control => e => {
  const track = document.querySelector('.carousel_images');
  const slides = Array.from(track.children);
  console.log(slides);
  const moveSlide = (currentSlide, targetSlide, direction) => {
    console.log(currentSlide, targetSlide);
    if (direction === 'left') {
      translateX += targetSlide.clientWidth;
      track.style.transform = `translateX(${translateX}px)`;

      console.log(targetSlide.clientWidth);
      console.log(window.innerWidth);
    }
    if (direction === 'right') {
      translateX -= targetSlide.clientWidth;
      track.style.transform = `translateX(${translateX}px)`;

      console.log(targetSlide.clientWidth);
    }
    currentSlide.classList.remove('current-slide');
    targetSlide.classList.add('current-slide');
  };

  if (control === 'left') {
    const currentSlide = document.querySelector('.current-slide');
    const prevSlide = currentSlide.previousElementSibling;
    console.log(currentSlide, prevSlide);
    if (prevSlide === null) {
      return;
    }
    moveSlide(currentSlide, prevSlide, control);
  }

  if (control === 'right') {
    const currentSlide = document.querySelector('.current-slide');
    const nextSlide = currentSlide.nextElementSibling;
    if (nextSlide === null) {
      return;
    }
    moveSlide(currentSlide, nextSlide, control);
  }
};
