  const handleClickAccordian = accordian => e => {
    if (!e.target.nextSibling.classList.contains('showPanel')) {
      Array.from(document.querySelectorAll('.panel')).map(a => {
        a.classList.remove('showPanel');
        a.previousSibling.classList.remove('activeAccordian');
        return '';
      });
      Array.from(document.querySelectorAll('.accordian span')).map(
        a => {
          if (a.classList.contains('rotateChevUp')) {
            a.classList.remove('rotateChevUp');
            a.classList.add('rotateChevDown');
          }
          return '';
        },
      );
      if (e.target.children[0].classList.contains('rotateChevDown')) {
        e.target.children[0].classList.remove('rotateChevDown');
      }
      e.target.nextSibling.classList.add('showPanel');
      e.target.children[0].classList.add('rotateChevUp');
      e.target.classList.add('activeAccordian');
    } else {
      e.target.nextSibling.classList.remove('showPanel');
      e.target.children[0].classList.remove('rotateChevUp');
      e.target.classList.remove('activeAccordian');
      e.target.children[0].classList.add('rotateChevDown');
    }
  };

